import React, { useEffect, useState } from "react";
import { Admin } from "./Admin";
import { Menu } from "./components/Menu/Menu";
import { Container, Row, Col } from "react-bootstrap";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import { PrivateRoute } from "./PrivateRoute";
import { useSelector, useDispatch } from "react-redux";
import { Login } from "./components/Login/Login";
import { SignUp } from "./components/SignUp/SignUp";
import { logoutUser } from "./store/auth/actions";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";

export const App = () => {
  const [user, setUser] = useState(false);
  const authAcc = useSelector((store) => store.auth.currentUser);
  const dispatch = useDispatch();
  useEffect(() => {
    if (Object.keys(authAcc).length > 0) {
      setUser(true);
    }
  }, [authAcc]);

  const changeIsLogin = (isLogin) => {
    localStorage.removeItem("token");
    dispatch(logoutUser());
    setUser(isLogin);
  };

  return (
    <BrowserRouter>
      <div className="wrapper">
        <Container fluid className="main-container">
          <Row>
            <Col lg="12" className="top_menu">
              <Menu isAuth={user} changeIsLogin={changeIsLogin} />
            </Col>
            <Switch>
              <Route exact path="/login" component={Login} />
              <Route exact path="/signup" component={SignUp} />
              <PrivateRoute path="/admin" component={Admin} authed={user} />
            </Switch>
          </Row>
        </Container>
      </div>
    </BrowserRouter>
  );
};
