import React from "react";
import DynamicVariables from "../DynamicVariables/DynamicVariables";
import BrandSort from "../BrandSort/BrandSort";
import Daily from "../Daily/Daily";
import Compaigns from "../Compaigns/Compaigns";
import Reports from "../Reports/Reports";
import Overview from "../Overview/Overview";
import { DatePicker } from "../DatePicker/DatePicker";
import { Switch, Route } from "react-router-dom";
import { Assets } from "../Assets/Assets";
import { CSSTransition } from "react-transition-group";
import "./styles.css";

const Router = () => {
  return (
    <div className="main-content">
      <div className="top-line">
        <DatePicker />
        <BrandSort />
      </div>
      <Switch>
        <Route exact path="/admin/overview">
          {({ match }) => (
            <CSSTransition
              in={match != null}
              timeout={3000}
              classNames="my-node"
              unmountOnExit
            >
              <div className="page">
                <Overview />
              </div>
            </CSSTransition>
          )}
        </Route>
        <Route
          exact
          path="/admin/dynamic_variable"
          component={DynamicVariables}
        />
        <Route exact path="/admin/daily">
          {({ match }) => (
            <CSSTransition
              in={match != null}
              timeout={1000}
              classNames="my-node"
              unmountOnExit
            >
              <Daily />
            </CSSTransition>
          )}
        </Route>
        <Route exact path="/admin/compaigns">
          {({ match }) => (
            <CSSTransition
              in={match != null}
              timeout={1000}
              classNames="my-node"
              unmountOnExit
            >
              <Compaigns />
            </CSSTransition>
          )}
        </Route>
        <Route exact path="/admin/assets">
          {({ match }) => (
            <CSSTransition
              in={match != null}
              timeout={1000}
              classNames="my-node"
              unmountOnExit
            >
              <Assets />
            </CSSTransition>
          )}
        </Route>
        <Route exact path="/admin/reports">
          {({ match }) => (
            <CSSTransition
              in={match != null}
              timeout={300}
              classNames="my-node"
              unmountOnExit
            >
              <Reports />
            </CSSTransition>
          )}
        </Route>
      </Switch>
    </div>
  );
};

export default Router;
